import { Component, OnInit, Input } from '@angular/core';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
// import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import { Label } from 'ng2-charts';
import { RecoService } from 'src/app/services/reco.service';

@Component({
  selector: 'app-barchart',
  templateUrl: './barchart.component.html',
  styleUrls: ['./barchart.component.css']
})
export class BarchartComponent implements OnInit {
  @Input() data: any; 
  @Input() options: any; 
  @Input() type: any; 
   @Input() color: any; 


  public barChartOptions: ChartOptions = {
                              
    title:{
        display:true,
        text:this.options,
        fontSize:14
      },
       responsive: true,
    maintainAspectRatio: false,
      legend:{
        display:false,
        position:'right',
        labels:{
          fontColor:'#000'
        }
      },
      layout:{
        padding:{
          left:50,
          right:0,
          bottom:0,
          top:0
        }
      },
     
      scales: {
        xAxes: [{
            gridLines: {
              lineWidth: 0
            },
            scaleLabel: {
              display: false,
              labelString: 'PU2AB008'
            }
           
        }],
        yAxes: [{
          scaleLabel: {
            display: false,
            labelString: 'No of Delay (Days)'
          }
        }]
        
    }
  };
   
    // We use these empty structures as placeholders for dynamic theming.
   
  public barChartLabels: Label[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType: ChartType = this.type?this.type:'bar';
  public barChartLegend = true;
  // public barChartPlugins = [pluginDataLabels];

  public barChartData: ChartDataSets[] = [
    { data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' },
    { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];
  /*sampledata
      [
    { data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' },
    { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];
  */

  constructor(private _service:RecoService) { }
  chartColors = [
    {
      backgroundColor: '#626ed4',
      borderColor: '#626ed4',
      pointBackgroundColor: '#626ed4',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(103, 58, 183, .8)',
      fill: false
    },
    // ...colors for additional data sets
    ];
  ngOnInit() {
    // console.log("bar char ",this.data)
  }
  ngOnChanges() {
    this.barChartType= this.type?this.type:'bar';
      if(this.type === 'bar')
      {
 

        this.barChartOptions= {
                              
          title:{
              display:true,
              text:this.options,
              fontSize:14
            },
            legend:{
              display:false,
              position:'right',
              labels:{
                fontColor:'#000'
              }
            },
            layout:{
              padding:{
                left:50,
                right:0,
                bottom:0,
                top:0
              }
            },
           
            scales: {
              xAxes: [{
                  gridLines: {
                    lineWidth: 0
                  },
                  scaleLabel: {
                    display: false,
                    labelString: 'PU2AB008'
                  }
                 
              }],
              yAxes: [{
                gridLines: {
                 
                  borderDash: [8, 4],
                },
                scaleLabel: {
                  display: false,
                  labelString: 'No of Delay (Days)'
                }
              }]
              
          }
        };
        this.barChartLabels=this.data.label;
        if(this.color)
        {
          this.barChartData=[{data:this.data.data, backgroundColor:this.color,
          hoverBackgroundColor: this.color}]
        }
        else{
          this.barChartData=[{data:this.data.data, backgroundColor:'#ff5757',
          hoverBackgroundColor: '#ff5757'}]
        } 
      }
      else if(this.type === 'pie')
      { 
        // this.barChartOptions.title.text=this.options;
        this.barChartLabels=this.data.label;
        this.barChartData=[{data:this.data.data}];
        this.barChartOptions={
                              
          title:{
              display:true,
              text:this.options,
              fontSize:14
            },
          elements: {
            line: {
                    fill: false
            }
        }
        };
      }
      else if(this.type === 'line')
      { 
        
        this.barChartLabels=this.data.label;
        this.barChartData=[{data:this.data.data}];
        this.barChartOptions= {
                              
          title:{
              display:true,
              text:this.options,
              fontSize:14
            },
            legend:{
              display:false,
              position:'right',
              labels:{
                fontColor:'#000'
              }
            },
            layout:{
              padding:{
                left:50,
                right:0,
                bottom:0,
                top:0
              }
            },
           
            scales: {
              xAxes: [{
                  gridLines: {
                    lineWidth: 0,
                    borderDash: [8, 4],
                  },
                  scaleLabel: {
                    display: false,
                    labelString: 'PU2AB008'
                  }
                 
              }],
              yAxes: [{
                gridLines: {
                 
                  borderDash: [8, 4],
                },
                scaleLabel: {
                  display: false,
                  labelString: 'No of Delay (Days)'
                }
              }]
              
          }
        };
      }
      
     
     
    
  }
  // events
  public chartClicked({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public chartHovered({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public randomize(): void {
    // Only Change 3 values
    const data = [
      Math.round(Math.random() * 100),
      59,
      80,
      (Math.random() * 100),
      56,
      (Math.random() * 100),
      40];
    this.barChartData[0].data = data;
  }

}
