import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SharedModule } from './shared/shared.module';
import { MatDialogModule, MatFormFieldModule, MatInputModule, MatDatepickerModule, MatPaginatorModule, MatCardModule, MatButtonModule, MatCheckboxModule, MatGridListModule, MatRadioModule } from '@angular/material';
import { FormsModule } from '@angular/forms';
import { ChartsModule } from 'ng2-charts';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AgGridModule } from 'ag-grid-angular';
import { RouterModule, Routes } from '@angular/router';
// Import your library
import { OwlModule } from 'ngx-owl-carousel';
import { SidemenuComponent } from './shared/sidemenu/sidemenu.component';
import { ExpandMenuDirective } from './expand-menu.directive';
import { OrderComponent } from './kpi/order/order.component';
import { InvoiceComponent } from './kpi/invoice/invoice.component';

const appRoutes: Routes = [
  {
    path: 'main',
    component: SidemenuComponent,
   
    children: [
      {
        path: 'dash',
        component: DashboardComponent,
      },
      {
        path:'kpi',
        children:[
          {
            path: 'order',
            component: OrderComponent,
          },
          {
            path: 'invoice',
            component: InvoiceComponent,
          }


        ]
      }
     ,
     
      

      {
        path: '',
        redirectTo: 'dash',
        pathMatch: "full"
      }
    ]
  },

{
  path: '',
  redirectTo: '/main/dash',
  pathMatch: 'full'
},
// { path: '**', component: PageNotFoundComponent }
];
@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    OrderComponent,
    InvoiceComponent,
    // ExpandMenuDirective
  ],
  imports: [
    SharedModule,
    MatGridListModule,
    MatRadioModule,
    MatDialogModule,
    MatFormFieldModule,
    MatInputModule,
    MatDatepickerModule,
    MatPaginatorModule,
    BrowserModule,
    FormsModule,
    OwlModule,
    MatCardModule,
    ChartsModule,
    MatButtonModule,
     MatCheckboxModule,
    HttpClientModule,
    BrowserAnimationsModule,
    AgGridModule.withComponents(null),
    RouterModule.forRoot(appRoutes, { useHash: true, enableTracing: false })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
