import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from  '@angular/common/http';
import { map } from 'rxjs/operators';
import * as moment from 'moment';
@Injectable({
  providedIn: 'root'
})
export class RecoService {

 
  public httpOptions : any;

  constructor(private _http : HttpClient) {
    //Http Headers Options
    this.httpOptions = {
      headers: new HttpHeaders (
        { 'Content-Type': 'application/json; charset=utf-8',
          'BrowserToken' : 'auth_Token'})
    }
  }

  gettingERP(data)
  {
    let obj={
      relationshipId:data
    };
    // return this._http.put('http://localhost:3000/api/web/signin',data)
  
    return this._http.put<any>('http://localhost:6001/api/web/getWidgetsData', obj)
    .pipe(map(res => {
      let label=[]
      let data=[];
      for (let item of res) {
        // console.log(item); // 9,2,5moment().format("MMM Do YY");
        label.push(moment(item.referenceDateTime_1).format("MMM Do YY"));
        data.push(item.DateCount);
       }
       let obj={
         label:label,
         data:data
       }
        return obj;
    }));
  }

  ERP(obj)
  {
    // let obj={
    //   offset:offset,
    //   limit:limit
    // };
    // return this._http.put('http://localhost:3000/api/web/signin',data)
  
    return this._http.post<any>("http://localhost:6001/api/web/ERP", obj)
    .pipe(map(res => {
      
        return res;
    }));
  }
  ERPTotalCount(obj)
  {
    // 
    return this._http.post<any>("http://localhost:6001/api/web/Erpvolume",obj)
    .pipe(map(res => {
      let temp=res;
      let pager= Math.round(temp.COUNT/25)
      
        return temp.COUNT;
    }));

  }
  gettingQueryValues(query)
{
  let currentUser:any = localStorage.getItem('currentUser');
  
  let obj ={
    userId:currentUser.userId,
    moduleId:currentUser.moduleId,
    query:query
  }

  return this._http.put<any>('http://localhost:6001/api/web/query', obj) 
}
  ListBankName()
  {
    
    return this._http.get<any>("http://localhost:6001/api/web/listrelationship")
    .pipe(map(res => {
      
        return res;
    }));


  }

}
